<template>
  <div>
    <div class="crumbs" style="display:flex;justify-content:center;">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-s-order"></i> 
        </el-breadcrumb-item>
        <el-breadcrumb-item> <i></i> </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <!---->

      <el-form
        label-width="120px"
        style="display:flex;justify-content:center;align-items:center "
      >
        <el-form-item label="">
          <el-select
            v-model="selectedClassRoom"
            style="width: 300px"
            placeholder=""
            @change="displayCard"
          >
            <el-option
              v-for="item in classRoomList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-divider></el-divider>

      <!--Card-->
      <div
        v-if="chooseClassRoom"
        style="display:flex; flex-wrap:wrap; justify-content:flex-start;"
      >
        <!---->
        <el-card
          v-for="(card, cardIndex) in classRoomCard_List"
          :key="card"
          class="box-card"
          @click="view_atdTable(card.begin_date)"
          shadow="hover"
        >
          <template #header>
            <div class="card-header">
              <span>{{ cardIndex + 1 }}</span>
              <el-button class="button" type="primary" plain
                ><i class="el-icon-document"></i>
                
              </el-button>
            </div>
          </template>
          <div class="card-content">
            <p>{{ card.name }}</p>
            <hr />
            <p>{{ card.begin_date }}</p>
            <hr />
            <p>{{ card.location }}</p>
          </div>
        </el-card>
        <!---->
        <el-card
          class="box-card "
          body-style="height:202.5px ; padding:0;background-color:#66b1ff;color:#FFF;display:flex;justify-content:center;align-items:center"
          @click="download_atdTable"
        >
          <h2><i class="el-icon-download"></i></h2>
        </el-card>
      </div>

      <!--drawer-->
      <el-drawer v-model="drawer">
        <template v-slot:title>
          <div style="color:#242f42; text-align:center;font-size:30px;">
            <b></b>
            <hr
              style="margin: 15px;border: none;height: 2px;background-color: #606266;"
            />
          </div>
        </template>
        <!--drawer-->
        <el-table
          class="table"
          :data="gridData"
          show-summary
          :summary-method="get_stuNum"
          :header-cell-style="{ 'text-align': 'center' }"
          :cell-style="{ 'text-align': 'center' }"
          :max-height="window_height"
        >
          <el-table-column property="name" label=""></el-table-column>
          <el-table-column property="id" label=""></el-table-column>
        </el-table>
      </el-drawer>
    </div>
  </div>
</template>

<script>
import http from "@/api/chatroom";
export default {
  //data is a function
  data() {
    return {
      userId: "",
      dataWareHouse: [],
      myClassRoom: [], //
      selectedClassRoom: "",
      classRoomList: [], //input
      classRoomCard_List: [], //card
      chooseClassRoom: false, //
      drawer: false, //
      //
      gridData: [],
      window_height: "", //max-height
    };
  },
  created() {
    this.userId = JSON.parse(localStorage.getItem("user")).id;
    //console.log(this.userId);
    this.getclassnames();
    this.window_height = document.body.clientHeight * 0.8;
  },

  //methods is a object
  methods: {
    //select
    /*---------------------------------------------------------------------*/
    getclassnames() {
      //
      http.findMyClassRooms(this.userId).then((res) => {
        this.dataWareHouse = res;
        //console.log(this.dataWareHouse);
        res.forEach((element) => {
          this.myClassRoom.push(element.name);
        });
        //classname
        this.myClassRoom = [...new Set(this.myClassRoom)];
        //console.log(this.myClassRoom);

        //classnameclassroomlist
        this.myClassRoom.forEach((ele) => {
          let classroom = {};
          classroom.label = ele;
          classroom.value = ele;
          //console.log(classroom);
          this.classRoomList.push(classroom);
        });
      });
    },

    //
    /*---------------------------------------------------------------------*/
    displayCard() {
      //selectedClassRoom
      //classRoomCard_Lit
      this.classRoomCard_List = []; //
      this.dataWareHouse.forEach((e) => {
        //console.log(this.selectedClassRoom);
        if (e.name === this.selectedClassRoom) {
          let classRoomCard = {};
          classRoomCard.name = e.name;
          classRoomCard.begin_date = e.start_TIME.split(" ")[0];
          classRoomCard.location = e.location;
          //console.log(classRoomCard);
          this.classRoomCard_List.push(classRoomCard);
        }
      });
      //console.log(this.classRoomCard_List);
      this.chooseClassRoom = true;
    },

    // drawer 
    /*---------------------------------------------------------------------*/
    view_atdTable(date) {
      //
      this.gridData = [];
      //date & course
      //console.log(date);
      let param = {
        params: {
          date: date,
          course: this.selectedClassRoom,
        },
      };
      // stu_list drawertable
      http.findBySearch(param).then((res) => {
        //console.log(res);
        res.data.stulist.forEach((e) => {
          let stu = {};
          stu.name = e.name;
          stu.id = e.number;
          this.gridData.push(stu);
        });
      });
      //drawer
      this.drawer = true;
    },

    // drawer
    /*---------------------------------------------------------------------*/
    get_stuNum(param) {
      //console.log(param);
      const { data } = param;
      const sums = [];
      sums[0] = ": ";
      sums[1] = data.length;
      return sums;
    },

    // 
    /*---------------------------------------------------------------------*/
    download_atdTable() {
      //
      let teachername = JSON.parse(localStorage.getItem("user")).name;
      let course = this.selectedClassRoom;
      //console.log(teachername);
      //console.log(course);
      let dates = [];
      this.classRoomCard_List.forEach((e) => {
        dates.push(e.begin_date);
      });
      dates = dates.join("&dates=");
      //console.log(dates);
      window.location.href =
        "http://39.106.39.49:8888/getexcel/?course=" +
        course +
        "&dates=" +
        dates +
        "&teachername=" +
        teachername;
    },
  },
};
</script>

